@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&family=Quicksand:wght@500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Libre+Barcode+128+Text&display=swap);
html, #root {
  height: 100%;
}

body {
  margin: 0;
  background: rgb(204,242,255);
  background: linear-gradient(90deg, rgba(204,242,255,1) 0%, rgba(234, 232, 232,1) 48%, rgba(255,226,232,1) 100%);
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  display: flex;
  flex-direction: column;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --font-content: 'Quicksand', sans-serif;
  --font-bar: 'Libre Barcode 128 Text', cursive;
  --pink:rgb(255, 0, 149);
  --blue:  rgb(0, 195, 255);
  --blue-link: rgb(0, 119, 255);
}
.navbar-container {
    background: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    flex-wrap: wrap;
}

.main-title {
	display: inline;
	position: relative;
    font-family: var(--font-content);
    font-weight: bold;
    font-size: 45px;	
    letter-spacing: -5px;
	color: rgba(255, 0, 149, 0.815);
}

.main-title:after {
	content: "a simple to do list";
    position: absolute; 
    left: -2px; 
    top: -2px;
	color: rgba(0, 195, 255, 0.781); 
}
.list-container {
    padding: 20px;
}

.form-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.todo-item-container {
    min-height: 40px;
    padding: 20px;
    display: flex;
    flex-wrap: wrap;
    font-family: var(--font-content);
    font-size: 20px;
    align-items: center;
    border-radius: 6px;
}

.todo-item-container:hover{
    min-height: 40px;
    padding: 20px;
    display: flex;
    flex-wrap: wrap;
    font-family: var(--font-content);
    box-shadow: 5px 9px 27px -7px rgba(0,0,0,1);
}


.todo-item-container:hover .b-contain input[type="checkbox"] ~ .b-input {
    transform: scale(1.3);
}

.item {
    margin-left: 15px;
	width: calc(100% - 50px);
}
.item-selected {
    margin-left: 15px;
	width: calc(100% - 50px);
	text-decoration: line-through;
	color: gray;
}

.b-contain *, .b-contain *::before, .b-contain *::after {
	box-sizing: content-box !important;
}

.b-contain input {
	position: absolute;
	z-index: -1;
	opacity: 0;
}

.b-contain {
	display: table;
	position: relative;
	padding-left: 30px;
	cursor: pointer;
	margin-bottom: 18px;
}

.b-contain input[type="checkbox"] ~ .b-input {
	position: absolute;
	top: 0;
	left: 0;
	height: 1.25rem;
	width: 1.25rem;
	background: rgb(255, 255, 255);
	transition: background 250ms;
	border: 1px solid rgba(184, 194, 204, 1);
	border-radius: 0.125rem;
}


.b-contain input[type="checkbox"] ~ .b-input::after {
	content: '';
	position: absolute;
	display: none;
	left: .45rem;
	top: .18rem;
	width: .25rem;
	height: .6rem;
	border: solid rgb(255, 240, 255);
	border-width: 0 2px 2px 0;
	transition: background 250ms;
	transform: rotate(45deg);
}

.b-contain input:checked ~ .b-input::after {
	display: block;
}

.b-contain:hover input ~ .b-input,
.b-contain input:focus ~ .b-input {
    background:rgb(255, 255, 255);
}

.b-contain input:focus ~ .b-input {
	box-shadow: 0 0 0 2px rgba(52,144,220,0.5);
}

.b-contain input:checked ~ .b-input {
	background: rgba(0, 130, 243, 1);
	border-color: rgba(0, 130, 243, 1);
}

.b-contain input:checked:focus ~ .b-input, .b-contain:hover input:not([disabled]):checked ~ .b-input {
	background: rgba(13, 143, 255, 1);
	border-color: rgba(13, 143, 255, 1);
}

.b-contain .b-input::before {
	content: '';
	display: block;
	position: absolute;
	left: 0;
	top: 0;
	width: 3rem;
	height: 3rem;
	margin-left: -0.85rem;
	margin-top: -0.85rem;
	background: rgba(0, 130, 243, 1);
	border-radius: 2rem;
	opacity: .6;
	z-index: 99999;
	transform: scale(0);
}

@keyframes b-ripple {
	0% {
		transform: scale(0);
	}

	20% {
		transform: scale(1);
	}

	100% {
		opacity: 0;
		transform: scale(1);
  	}
}

@keyframes b-ripple-duplicate {
	0% {
		transform: scale(0);
	}

	30% {
		transform: scale(1);
	}

	60% {
		transform: scale(1);
	}

	100% {
		opacity: 0;
		transform: scale(1);
  	}
}

.b-contain input + .b-input::before {
	animation: b-ripple 250ms ease-out;
}

.b-contain input:checked + .b-input::before {
	animation-name: b-ripple-duplicate;
}

.b-contain .b-input::before {
	visibility: hidden;
}

.b-contain input:focus + .b-input::before {
	visibility: visible;
}

.b-contain:first-child .b-input::before {
	visibility: hidden;
}

.input-container {
    width: 100%;
    display: flex;
    margin: 20px 20px 20px;
}

input {
    width: 100%;
    height: 36px;
    border-radius: 6px;
    border: none;
    padding-left: 10px;
    font-family: var(--font-content);
}

input:focus {
    outline: none;
    box-shadow: 4px 6px 10px 0px rgba(0,0,0,0.75);
    transform: translateY(-2px);
}
.button-container {
    width: 60px;
}

button {
    width: 100%;
    height: 38px;
    background-color: rgb(56 162 236);
    border-radius: 6px;
    border: none;
    color: rgb(255, 0, 149);
    font-size: 24px;
    font-weight: bold;
    font-family: var(--font-content);
    box-shadow: 4px 4px 0px -1px rgba(255, 0, 149, 0.8);
    opacity: 0.8;
}

button:active {
    box-shadow: none;
    transform: translate(2px, 2px);
}

button:focus{
    outline: none;
}
.footer-container {
    position: absolute;
    bottom: 28px;
    display: flex;
    justify-content: center;
    width: 100%;
    height: 60px;
    align-items: center;
}

.credits {
    font-family: var(--font-bar);
    font-size: 45px;
    margin-right: 30px;
}

.social-media {
    cursor: pointer;
    margin-left: 15px;
}
.app-container {
  height: 100%;
}
